.active{
    border-bottom: 4px solid var(--baseColor);
    /* padding-bottom: 8px; */
}
.navbar {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 18px;
    /* box-shadow: 1px 2px 5px lightgray; */
    position: fixed;
    width: 100%;
    z-index: 1000;
    top: 0;
    /* background: white; */
}

.companyTag {
   color: white;
    cursor: pointer;
    text-decoration: none;
   
    margin-left: 40px;
    white-space:nowrap;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
}
.companyTag img{
    height: 25px;
    width: 25px;
    margin-right: 20px;
}
.companyTag span{
    color: var(--baseColor);
}

.nav-side-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    gap: 0 10px;
    width: 70vw;
    justify-content: end;
    margin-right: 35px;
   
}

.nav-items {
    display: flex;
    align-items: center;
    height: 80px;
}

.nav-links {
    color: white;
    text-decoration: none;
    /* padding: 8px 20px; */
    /* width: 126px; */
    margin-right: 20px;

}


.nav-links:hover {
    /* background-color: #000; */
    /* background: #f9a61a; */
    /* border-radius: 5px; */
    /* padding: 8px 20px; */
    border-bottom: 4px solid var(--baseColor);
    /* padding-bottom: 8px; */
   
}

.nav-links-button {
    display: none;
}


.dropdown-menu {
    /* width: auto; */
    position: absolute;
    top: 50px;
    list-style: none;
    text-align: start;
    min-width: 150px;
    /* width: 150px; */
    /* margin-left: -50px; */
    transition: all 0.5s;
    animation: zoom 0.5s;
    background: transparent;
    backdrop-filter: blur(5px);

    /* font-size: 15px; */
    
}

.dropdown-menu li{
    cursor: pointer;
    /* margin-left: 30px; */
    transition: 0.5s all;
    /* background: transparent; */
    backdrop-filter: blur(5px);
    border-radius: 5px;
}

.dropdown-menu li:hover {
    /* background: #f9a61a; */
    color:  #f9a61a;
    font-weight: bold;
}
.dropdown-menu li a:hover{
    color:  #f9a61a;
    font-weight: bold;
}

.drop-menu {
    display: none;
    transition: 0.5s all;
    animation: zoom 0.5s;
}

.menu-items {
    display: grid;
    text-decoration: none;
    color: #fff;
    padding: 15px;
    transition: 0.5s all;

}

.menu-icon{
    color: white;
    display: none ;
}

/* -------------------------------------------------------------------------mobile view -------------------------------------------------------- */


@media screen and (max-width: 800px) {
    .nav-side-menu {
        padding: 0;
        display: flex;
        flex-direction: column;
        width: 250px;
        position: absolute;
        margin-top: 0;
      
        /* border: 2px solid #ececee; */
        opacity: 1;
        justify-content: flex-start;
        margin: 0;
        background: #fff;
        right: 0;
        top: 50px;
        opacity: 1;
        transition: all 0.5s ease;
        overflow: hidden;
        width: 0;
        background: transparent;
        color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  /* background: black; */
  backdrop-filter: blur(5px);
  /* border-radius: 5px; */

        /* opacity: 0; */
    }

    .nav-side-menu.start {
        /* background: #fff;
        right: 0;
        top: 70px;
        opacity: 1;
        transition: all 0.5s ease; */
        transition: 0.5s all;
        width: 230px;
    
    }

    .nav-links {
        text-align: center;
        /* padding: 25px; */
        width: 100%;
        color: white;
        display: table;
        transition: all 0.5s;
        padding: 8px 20px;
        background: transparent;

    }

    .nav-links:hover {
        color: #fff;
        background:transparent;
    }

    .logo {
      width: 80%;
    }

    .menu-icon {
       display: revert;
       margin-right: 16px;
       cursor: pointer;
      
    }


    .nav-links-button {
        display: block;
        /* padding: 12px 30px; */
        margin: 25px auto;
        border-radius: 10px;
        background: #000;
        text-decoration: none;
        color: #fff;
        font-size: 1rem;
    }

   

    .nav-items {
        height: auto;
    }

  
    .navLogo{
        display: none;
    }
   
  
   .companyTag{
    margin-left: 15px;
   }

   .nav-links:hover {
    border-bottom: none;
}
.nav-items{
  opacity: 0;
  transition: 0.5s all;
}

.dropdown-menu {
    /* width: auto; */
    position: relative;
    top: 20px;
    list-style: none;
    text-align: start;
    white-space: nowrap;
    /* animation: none; */
    padding: 0;
    animation: fade 0.5s;
    margin-left: -50px;
    /* width: 100%; */
}

.dropdown-menu li{
    background: transparent;
    cursor: pointer;
    margin-left: 30px;
    transition: 0.5s all
}

}



