
.home{
    min-height: 100vh;
    background: var(--homeBack);
    padding-top: 25vh;
    color: white;
    background-repeat: no-repeat;
    background-size: cover;
}

.home .slogan{
    font-size: 75px;
    margin-left: 50px;
    font-weight: bold;
    position: relative;
    z-index: 1;
}

.home .slogan span{
    font-size: 75px;
    color: var(--baseColor);
}

.home .sloganParent{
    position: relative;
}
.home .orangeBack{
    background: var(--baseColor);
    height: 50px;
    width: 240px;
    top: 40px;
    left: 60px;
    z-index: 0;
    position: absolute;
}

.home .focusCont{
    margin-left: 50px;
    font-size:35px;
    width: 480px;
    font-weight: bold;
    margin-top: 15px;
    white-space: nowrap;
    display: flex;
    justify-content:left;
}
.home .focusCont div:nth-child(1){
    color: var(--baseColor);
}

.home .mission{
    width: 370px;
    text-align: justify;
    hyphens: auto;
    margin-left: 50px;
    margin-top: 10px;
}
.home .btnDiv{
    margin: 25px 0 0 50px;
    width: 350px;
}

.home .btnDiv .exploreBtn{
    background: var(--baseColor);
    padding: 10px 20px;
    color: black;
    font-weight: bolder;
    border: 4px solid var(--baseColor);
    cursor: pointer;
}

.home .btnDiv .requestBtn{
    /* background: var(--baseColor); */
    padding: 10px 20px;
    font-weight: bolder;
    border: 4px solid white;
    cursor: pointer;
}

.home .btnDiv .exploreBtn a{
    text-decoration: none;
    color: black;
}
.home .btnDiv .requestBtn a{
    text-decoration: none;
    color: white;
}

.home .quote{
    margin: 120px 0px 0px 50px;
}


.home .focusCont .myWordContPar{
    /* width: 300px; */
    height: 50px;
    overflow: hidden;
    margin-left: 20px;
    border-radius: 5px;
  }
  .home .focusCont .myWordContPar .myWordCont{
    width: 100%;
    color: white !important;
    height: 100%;
    /* background-color: #f9a61a; */
    display: flex;
    align-items: center;
    /* justify-content: center; */
    /* font-size: 40px; */
    white-space: nowrap;
    font-weight: bolder;
    transition: all 0.9s;
    overflow: hidden;
    border-radius: 5px;
  
  
  }

@media screen and (max-width:800px) {
    .home .focusCont{
        margin-left: 20px;
        justify-content: left;
        font-size:27px;
        width: 100vw;
        /* border: 2px solid black; */
    }
    .home .slogan{
        margin-left: 20px;
    }
    .home .mission{
        margin-left: 20px;
        width: 350px;
    }

    .home .btnDiv{
        width: 350px;
        margin-left: 20px
    }

    .home .quote{
        margin-left: 20px;
        font-size: 15px;
        white-space: nowrap;
    }
    .home .orangeBack{
        left: 35px;
    }
}