@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

*{
    font-family: Roboto;
    box-sizing: border-box;
  }
  html{
    scroll-behavior: smooth;
  }
  body{
  padding: 0;
  margin: 0;
  /* overflow: hidden; */
  }
  #root{
    overflow: hidden;
  }
  
  :root{
    --baseColor:#ff9000;
    --homeBack:url("../images/homeback.png");
    --servicesBack:url("../images/homeback.png");
    --teamBack:url("../images/homeback.png");
    --aboutUsBack:white;
  }

  .flex{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  @keyframes zoom {
    0%{
        transform: scale(0.1);
    }
    100%{
        transform: scale(1);
    }
}
  
  @import url('./navbar.css');
  @import url('./home.css');
  @import url('./services.css');
  @import url('./team.css');
  @import url('./aboutUs.css');
  
  
  
  