.team{
    min-height: 100vh;
    padding: 50px;
    background: var(--aboutUsBack);
}
.team .textDiv{
    position: relative;
    font-size: 32px;
    font-weight: bolder;
    white-space: nowrap;

}

.team .textDiv div:nth-child(1) {
    background: var(--baseColor);
    height: 30px;
    left: -5px;
    bottom: -10px;
    width: 200px;
    position: absolute;
    z-index: 0;
}

.team .textDiv div:nth-child(2) {
    z-index: 1;
}

.team .teamHeadingParent{
    margin: 50px 50px;
    display: flex;
    justify-content: center;
}



.team .teamHead{
    justify-content: center !important;
}


.team .teamHeadImg{
    width: 200px;
    height: 200px;
    /* border: 2px solid black; */
    border-radius: 50%;
    margin-right: 30px;
    overflow: hidden;
    transition:all 0.25s;

}

.team .teamHeadImg img{
    /* transition: all 0.25s; */
}

.team .teamHeadImg:hover{
    border-radius: 15px;
}

.team .teamHeadImg img{
    height: 100%;
    width: 100%;
}
.team .teamHeadText{
    font-size: 18px;
    width: 60%;
    /* border: 2px solid black; */
    text-align: justify;
    hyphens: auto;
}
.team .teamMembers{
    width: 80%;
    margin-left: 10%;
    display: flex;
    justify-content: space-around;
    text-align: center;
    /* border: 2px solid black; */
    margin-top: 50px;
}

.team .teamMembers .teamCard{
    height: 120px;
    width: 120px;
    border-radius: 50%;
    /* overflow: hidden; */
    cursor: pointer;
    position: relative;
    display: flex;
    font-size: 0px;
    /* box-shadow: 0 0 20px 0 rgba(132,144,255,.2); */

    /* overflow: hidden; */
}
.team .teamCard img{
    height: 120px;
    width: 120px;
    transition:all 0.5s;
    border-radius: 50%;
    z-index: 1;
}

.team .teamCard .memberName{

    transition: all 0.25s;
    position: absolute;
    display: flex;
    width: 100%;
    text-align: center;
    justify-content: center;
    bottom: 25px;
    font-weight: bold;
    white-space: nowrap;
   
    /* border: 2px solid black; */

}
.team .teamCard:hover .memberName{
    transform: translateY(20px);
    font-size: 15px;
    

}
.team .teamCard:hover img{
    transform: translateY(-30px);
    border-radius: 15px;
}
.team .teamCard:hover{
    overflow: revert;
}


@media screen and (max-width:800px){
    .team  .teamHead{
        flex-direction: column;
    }
    .team .teamHeadImg{
        margin-bottom: 20px;
    }
    .team .teamHead .teamHeadText{
        width: 95%;
    }

    .team .teamMembers .teamCard{
        height: 60px;
        width: 60px;
    }
    .team .teamCard img{
        height: 60px;
        width: 60px;
    }

    .team .teamMembers{
        width: 100%;
        margin-left: 0;
    }

    .team .teamCard:hover .memberName{
        font-size: 10px;
    }
    .team .teamHeadImg{
        margin-right: 0;
    }
}