.services{
    min-height: 100vh;
    background: var(--servicesBack);
    border: 2px solid black;
}

.servicesCont{
    padding: 50px 90px;
    flex-wrap: wrap;
}
.services .servicesGif img{
    width: 400px;
    width: 33vw;
    margin-top: 10px;
    border-radius: 10px;
}

.services .servicesCont .servicesText{
    font-size: 40px;
    color: white;
    border-left: 8px solid var(--baseColor);
    padding-left: 10px;
}

.services .servicesCont .servicesText.right{
    text-align: right;
    border-right: 8px solid var(--baseColor);
    padding-right: 10px;
    border-left: none;
    
}

.services .servicesCont .servicesText .textDiv{
    position: relative;
}
.services .servicesCont .servicesText .textDiv div:nth-child(1){
    background: var(--baseColor);
    height: 40px;
    left: 50px;
    width: 180px;
    position: absolute;
    z-index: 0;
}

.services .servicesCont .servicesText .textDiv div:nth-child(2){
    z-index: 1;
}

.services .servicesCont .servicesText.right .textDiv{
    justify-content: right;
}
.services .servicesCont .servicesText.right .textDiv div:nth-child(1){
    left: 130px;
    width:200px
}
.serviceCard{
    margin-top: 30px;
}


@media screen and (max-width:800px) {
    .servicesCont{
        padding: 0px;
        flex-direction: column;
    }

    .services .servicesGif img{
        width: 350px;
        margin-top: 10px;
    }
    
}