.aboutUs{
    min-height: 100vh;
    background: var(--aboutUsBack);
    /* border: 2px solid black; */
}

.aboutUs .aboutUsHeadingParent{
    margin: 50px 50px;
    display: flex;
    justify-content: center;
}

.aboutUs .textDiv{
    position: relative;
    font-size: 32px;
    font-weight: bolder;
}

.aboutUs .textDiv div:nth-child(1) {
    background: var(--baseColor);
    height: 30px;
    left: -5px;
    bottom: -10px;
    width: 200px;
    position: absolute;
    z-index: 0;
}

.aboutUs .textDiv div:nth-child(2) {
    z-index: 1;
}



.aboutUsCont .aboutUsLeft{
    width: 50%;
}
.aboutUsCont .aboutUsRight{
    width: 50%;
}

.aboutUsCont .content{
    padding: 20px;
    text-align: justify;
    hyphens: auto;
}
.aboutUsLeft{
    padding: 50px;
}
.aboutUsLeft img{
    width:90%;
    border-radius: 10px;
}

.textInGif{
    position: absolute;
    bottom: 8%;
    left: 2%;
    background-color: #023681;
    color: white;
    font-size: 32px;
    width: 70%;
    padding: 40px 30px;
    text-align: center;
    border-radius: 10px;
}

.aboutUs .smallText{
    font-weight: bold;
    font-style: italic;
    padding: 30px;
}

@media screen and (max-width:800px) {
    .aboutUsCont{
        flex-direction: column-reverse;

    }
    .aboutUsCont .aboutUsLeft{
        width: 100%;
    }


    .aboutUsCont .aboutUsRight{
        width: 100%;
    }
    .aboutUsLeft{
        padding: 0;
    }
    .aboutUsLeft img{
        width:100%;
    }

    .textInGif{
        padding: 0;
        font-size: 20px;
    }
}